import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import {
  TextInput,
  NumberInput,
  ComposedModal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Button,
  Icon
} from  'carbon-components-react';
import { 
  iconDelete, iconDeleteSolid, iconDeleteOutline, 
} from 'carbon-icons';

// This defines a modal controlled by a launcher button. We have one per DataTable row.
const ModalStateManager = ({
  renderLauncher: LauncherContent,
  children: ModalContent,
}) => {
  const [open, setOpen] = useState(false);
  return (
    <>
      {!ModalContent || typeof document === 'undefined'
        ? null
        : ReactDOM.createPortal(
            <ModalContent  
              open={open} 
              setOpen={setOpen} 
            />,
            document.body
          )
      }
      {LauncherContent && 
      <LauncherContent 
        open={open} 
        setOpen={setOpen} 
      />
      }
    </>
  );
};

// Modal properties
const deleteProps = {
  composedModal: ({ titleOnly } = {}) => ({
    open: true,
    danger: true,
    selectorPrimaryFocus: '[data-modal-primary-focus]',
  }),
  modalHeader: ({ titleOnly, name } = {}) => ({
    label: 'Enabled Languages',
    title: 'Delete Enabled Languages?',
    iconDescription: 'Close',
  }),
  modalBody: () => ({
    hasScrollingContent: false,
    'aria-label': 'Delete Enabled Languages?',
  }),
  modalFooter: () => ({
    primaryButtonText: 'Delete',
    primaryButtonDisabled: false,
    secondaryButtonText: 'Cancel',
    shouldCloseAfterSubmit: true,
    danger: true,
  }),
  menuItem: () => ({
    closeMenu: (event) => { handleSubmit(event); }
  }),
  deleteIcon: () => ({
    style: {
      margin: '5px',
    },
    icon: iconDelete,
    name: iconDelete,
    role: 'img',
    fill: 'grey',
    fillRule: '',
    width: '',
    height: '',
    description: 'This is a description of the icon and what it does in context',
    iconTitle: '',
    className: 'extra-class',
  }),
};

// On submit we should be passed the values, not have to look them up
const handleSubmit = (server_enabled_languages_enabled_languages,  loadGrid, setOpen) => {
 
  // Refresh data
  loadGrid(server_enabled_languages_enabled_languages,  false);

  return true;
}

// The implementation of the Modal
class Serverenabled_languagesDeleteModal extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      row: props.row,
      loadGrid: props.loadGrid,
      server_enabled_languages_enabled_languages: this.props.row.cells[0].value,
      open: false,
    }
  }

  render() {
    // const { open } = this.state.open;
    const { size, ...rest } = deleteProps.composedModal();
    const { hasScrollingContent, ...bodyProps } = deleteProps.modalBody();

    return (
      <ModalStateManager
        renderLauncher={({ setOpen }) => (
          <Icon {...deleteProps.deleteIcon()} onClick={() => setOpen(true)} title={this.state.id} />
        )}>
        {({ open, setOpen }) => (
          <ComposedModal
            {...rest}
            open={open}
            row={this.props.row}
            loadGrid={this.state.loadGrid}
            size={size || undefined}
            onClose={() => setOpen(false)}>
            <ModalHeader {...deleteProps.modalHeader({ titleOnly: true, name: this.state.code + ' ' + this.state.model })} />
            <ModalBody />
            <ModalFooter {...deleteProps.modalFooter()} shouldCloseAfterSubmit={true} onRequestSubmit={() => { handleSubmit(this.state.server_enabled_languages_enabled_languages, this.state.loadGrid, setOpen); }} />
          </ComposedModal>
        )}
      </ModalStateManager>
    );
  }

  
}

export default Serverenabled_languagesDeleteModal;
